/**
* FaqSection.js
* Faq Section to link to FAQ page
*
* @version 0.0.1
* @author [Nicolette Chan](nicolette@policypal.com)
*/
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';

// import FAQProfile from 'assets/images/Categories/faq_section_profile.png';
import FAQIcon from 'assets/images/Categories/faq_section_icon.svg';

// import button
import Button from '../FormsV3/Button/Button';

import './FaqSection.scss';

class FaqSection extends Component {
  render() {
    const { history } = this.props;
    return (
      <div className="faq-section">
        <div className="faq-section-bg">
          <div className="content">
            <Row className="center-align-row">
              <Col lg={{ size: 6, offset: 1 }} md="8" sm="12">
                <div className="faq-wrapper">
                  <div className="faq-icon">
                    <img src={FAQIcon} alt="faq-icon" />
                  </div>
                  {/* <div className="faq-profile">
                    <img src={FAQProfile} alt="faq-profile" />
                   </div> */}
                </div>
              </Col>
              <Col lg="5" md="8" sm="12" style={{ zIndex: 30 }}>
                <div className="faq-desc-wrapper">
                  <div className="title">
                    You have questions,
                    <br />
                    We have answers
                    <div className="desc">
                      Insurance is complicated.
                      We want you to be covered
                      and feel secure, without
                      paying for more than youâ€™ll need.
                    </div>
                    <Button
                      className="faq-btn"
                      variant="medium-color"
                      onClick={() => history.push('/faq')}
                      width={200}
                      height={72}
                    >
                      <p>FAQ</p>
                    </Button>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </div>
    );
  }
}

FaqSection.propTypes = {
  history: PropTypes.object.isRequired,
};

export default withRouter(FaqSection);
