import _ from 'lodash';
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { Row } from 'reactstrap';
import PropTypes from 'prop-types';

import CategoriesBGOverlay from 'assets/images/Categories/Categories_BG.png';
import CategoriesIcon from 'assets/images/NavIcons';

// import FAQ section component
import FaqSection from './FaqSection';
// import Subscribe section component
import SubscribeSection from './SubscribeSection';
// Footer
import FooterContainer from '../../containers/FooterContainer';

// import button
import Button from '../FormsV3/Button/Button';

import './Categories.scss';

import {
  onPageInsurance,
  setStateInsurance,
  onPageMore,
  setStateMore,
  onPageBusiness,
  setStateBusiness,
} from '../../actions/navActions';

class InsuranceCategories extends Component {
  constructor(props) {
    super(props);

    // Set Insurance Page Indicator
    const pageState = onPageInsurance.ACTIVE;
    // Reset More Page Indicator
    const resetMore = onPageMore.NONE;

    // Reset Business Page Indicator
    const resetBusiness = onPageBusiness.NONE;

    const {
      setPage,
      resetIndicatorMore,
      resetIndicatorBusiness,
    } = this.props;
    setPage(pageState);
    resetIndicatorMore(resetMore);
    resetIndicatorBusiness(resetBusiness);
  }
  componentDidMount() {
    window.scroll(0, 0);
  }
  renderCategoriesBtn() {
    const { history } = this.props;
    const catIcons = CategoriesIcon.color;
    const renderBtns = _.map(catIcons, (icons, index) => (
      <div className="cat-link" key={`icon-${index}`}>
        <Button
          className="cat-btn"
          type="submit"
          variant="medium-color"
          key={index}
          height={200}
          width={200}
          onClick={() => history.push(icons.url)}
        >
          <img
            src={icons.src}
            alt={`icon-${index}`}
            className="cat-icon"
          />
          <p><span style={{ display: 'block' }}>{icons.name}</span></p>
        </Button>
      </div>
    ));
    return renderBtns;
  }
  render() {
    window.scroll(0, 0);
    return (
      <div className="categories">
        <div>
          <div className="content">
            <div className="content-row">
              <div className="title">A plan for everyone</div>
              <div className="title-sec">Reputable and trusted companies to choose from</div>
            </div>
            <div>
              <img src={CategoriesBGOverlay} className="background-overlay" alt="bgOverlay" />
            </div>
            <Row className="cat-container">
              {this.renderCategoriesBtn()}
            </Row>
          </div>
        </div>
        <FaqSection />
        <SubscribeSection />
        <FooterContainer />
      </div>
    );
  }
}

InsuranceCategories.defaultProps = {
  setPage: null,
  resetIndicatorMore: null,
  resetIndicatorBusiness: null,
};

InsuranceCategories.propTypes = {
  setPage: PropTypes.func,
  resetIndicatorMore: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
  history: PropTypes.object.isRequired,
};

const mapDispatchToProps = dispatch => ({
  setPage: (pageState) => {
    dispatch(setStateInsurance(pageState));
  },
  resetIndicatorMore: (resetMore) => {
    dispatch(setStateMore(resetMore));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default withRouter(connect(null, mapDispatchToProps)(InsuranceCategories));
